.App {
  text-align: center;
  margin: 0;
  padding: 0;
  height: 100vh;
}
.App-logo {
  height: 100%;
  background-color: #1DA57A;
  pointer-events: none;
}
.App-header {
  text-align: left;
  background-color: #232325;
  min-height: 8%;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  color: white;
}
.ant-layout-header {
  height: 48px !important;
  line-height: 48px !important;
}
.Affix .ant-radio-button-wrapper {
  width: 120px;
  text-align: center;
}
.Affix .ant-btn {
  width: 100%;
  text-align: center;
}
.App-content {
  text-align: center;
  background-color: #292929;
  min-height: calc(100% -48px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-content .content-img {
  height: 100%;
  pointer-events: none;
}
